<template>
  <div :class="[{'wrapper':true},{'fontsS':cn},{'fonte':en}]">
    <div class="boxwrapper">
      <div
        @click="changeView('/Aboutsus/panter')"
        :class="['panter',{'active':npath=='/Aboutsus/panter'}]"
      >{{$t('Aboutus.topNav.panter')}}</div>
      <div
        @click="changeView('/Aboutsus/prombles')"
        :class="['prombles',{'active':npath=='/Aboutsus/prombles'}]"
      >{{$t('Aboutus.topNav.promble')}}</div>
    </div>

    <router-view></router-view>
  </div>
</template>

<script>
export default {
  mounted() {
    this.npath = this.$route.path;
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  beforeUpdate() {
    this.en = localStorage.getItem("locale") == "en" ? true : false;
    this.cn = localStorage.getItem("locale") == "zh" ? true : false;
  },
  methods: {
    changeView(p) {
      this.$router.push(p);
      this.npath = p;
    }
  },
  data() {
    return {
      npath: "",
      cn: true,
      en: false
    };
  }
};
</script>

<style lang="less" scoped>
.wrapper {
  .boxwrapper {
    display: flex;
    div {
      width: 50%;
      height: 1.32rem;
      text-align: center;
      line-height: 1.32rem;
      color: #9b9b9f;
      font-size: 0.36rem;
    }
    .active {
      background: #ff7000;
      color: #fff;
    }
  }
}
</style>